/* App.css */

.container {
    display: flex;
    padding: 10px;
    flex-direction: column;
    align-items: center;
    background-color: #FAFAFA;
    width: 380px;
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
  }
  
  .header {
    width: 100%;
    max-width: 1200px;
    background-color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .h1 {
    font-family: 'Times New Roman';
    font-size: 2rem;
    font-weight: 500;
    text-align: center;
    color: #333;
    margin: 0;
  }
  
  .h4 {
    font-family: 'Times New Roman';
    font-size: 1rem;
    font-weight: 500;
    text-align: center;
    color: #333;
    margin: 5px 0;
  }
  
  .divider {
    width: 360px;
    height: 1px;
    background-color: #E0E0E0;
    margin: 10px 0;
  }
  
  .scrollViewContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .cardsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .toggleButton {
    background-color: #333;
    padding: 10px;
    border-radius: 5px;
    margin-top: 10px;
    color: #FAFAFA;
    font-weight: 500;
    border: none;
    cursor: pointer;
  }
  
  .card {
    background-color: #FFF;
    border-radius: 5px;
    padding: 10px;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.1); 
    margin-bottom: 10px;
    width: 360px;
  }
  
  .footer {
    margin-top: 20px;
    background-color: #FAFAFA;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 360px;
    text-align: center;
    position: fixed;
    bottom: 0;
  }
  
  .modalContent {
    background-color: #FFF;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
    width: 400px;
    text-align: center;
  }
  
  .modalTitle {
    font-family: 'Times New Roman';
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 20px;
  }
  
  .closeButton {
    background-color: #333;
    padding: 10px;
    border-radius: 5px;
    color: #FAFAFA;
    font-weight: 500;
    border: none;
    cursor: pointer;
  }
  